.join-section {
  position: relative;
  margin-top: 0.7rem;
  padding-top: 4rem;
  padding-bottom: 5rem;

  @include tablet {
    margin-top: 3rem;
    padding-top: 14rem;
    padding-bottom: 5rem;
  }
}

.join {
  &__left {
    position: relative;
    z-index: 2;
  }

  &-section__title {
    color: var(--color-primary);
    font-size: 4.6rem;
    line-height: 5.5rem;
    margin-bottom: 1rem;

    @include mobile {
      font-size: 9.6rem;
      line-height: 11.5rem;
      margin-bottom: 2rem;
    }
  }

  &__text {
    font-size: 1.6rem;
    line-height: 1.8rem;
    color: var(--color-black);
    margin-bottom: 2.1rem;

    @include tablet {
      margin-bottom: 4.1rem;
    }
  }

  &__right {
    position: absolute;
    top: 55%;
    left: 61%;
    transform: translateY(-50%);
    z-index: 1;
  }
}
