.section-feautures {
  padding: 0 0 5rem 0;
}

.feautures__container {
  position: relative;
}

.feautures-title {
  color: var(--color-primary);
  position: static;
  top: 10.5rem;
  left: -11.2rem;
  font-family: var(--font-secondary);
  font-size: 7rem;
  font-weight: 400;
  margin-bottom: 2rem;

  @include tablet {
    font-size: 15.8rem;
  }

  @include desktop {
    position: absolute;
    transform: rotate(270deg);
    font-size: 10.8rem;
    margin-bottom: 0;
  }
}

.feautures-wrap {
  display: flex;
  margin: 0 -6rem;

  flex-direction: column;

  @include tablet {
    flex-direction: row;
    margin: 0 -2rem;
    justify-content: flex-end;
  }

  @include desktop {
    margin: 0 -6rem;
  }
}

.feautures {
  padding: 0px 6rem;

  width: 100%;
  margin-bottom: 4rem;

  @include tablet {
    width: 33%;
    padding: 0px 2rem;

    &:nth-child(2n) {
      margin-top: 5.5rem;
    }
  }

  @include desktop {
    width: 30%;
    padding: 0px 6rem;
  }

  &__img {
    img {
      display: block;
      width: 100%;
    }
  }

  &__name {
    font-family: var(--font-secondary);
    font-size: 4.8rem;
    line-height: 5.8rem;
    color: var(--color-black);

    @include tablet {
      font-size: 3.8rem;
    }
  }
}
