@import "../mixins.scss";

.header {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    padding: 1rem 0;

    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.nav {
    position: absolute;
    top: 4.5rem;
    left: -100%;
    background-color: white;
    width: 100%;
    height: 100vh;
    transition: all .3s ease;
    padding: 3rem 1.5rem;

    &.active {
        left: 0;
    }
    
    &__list {
        display: flex;
        flex-direction: column;

        @include tablet {
            flex-direction: row;
        }
    }

    @include tablet {
      position: static;
      width: auto;
      height: auto;
      background-color: transparent;
    }

    &__item {
        margin-bottom: 2rem;

        @include tablet {
           
            & + & {
                margin-left: 3rem;
            }
        }
    }

    &__link {
        font-family: var(--font-primary);
        font-size: 2rem;
        line-height: 1.8rem;
        font-weight: 500;
        color: var(--color-black);
        transition: color .3s ease;

        &:hover {
            color: var(--color-primary);
        }

        @include tablet {
            font-size: 1.6rem
        }
    }

}


.burger {
    @include burger;
    display: block;
    color: var(--color-black);
    z-index: 100;

   @include tablet {
       display: none;
   }
}
.scroll {
	position: relative;
	overflow: hidden;
	height: 100vh;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
}