@import '../mixins.scss';
.section-roadmap {
  padding: 5rem 0;
  background-color: var(--color-primary);
  position: relative;

  @include tablet {
    padding: 9rem 0;
  }

  .section-title {
    text-align: center;
    margin-bottom: 5rem;
    position: relative;
    z-index: 10;

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: -33%;
      z-index: -1;
      transform: translateY(45%);
      width: 100%;
      height: 8px;
      background-color: var(--color-white);
    }
  }
}

.roadmap {
  display: flex;
  align-items: center;
  padding: 1.7rem 1.3rem;
  border: 1px solid var(--color-white);
  background-repeat: no-repeat;
  background-position: right;
  background-size: cover;
  min-height: 17rem;
  margin-bottom: 2.1rem;

  @include tablet {
    padding: 2.7rem 3.3rem;
    background-size: contain;
  }

  &:nth-child(1) {
    background-image: url('../img/g1.png');
  }
  &:nth-child(2) {
    background-image: url('../img/g2.png');
  }
  &:nth-child(3) {
    background-image: url('../img/g3.png');
  }
  &:nth-child(4) {
    background-image: url('../img/g4.png');
  }
  &:nth-child(5) {
    background-image: url('../img/g5.png');
  }

  &:last-child {
    margin-bottom: 0;
  }

  &__title {
    font-family: var(--font-secondary);
    font-weight: 400;
    font-size: 5rem;
    line-height: 5.5rem;
    color: var(--color-black);

    @include tablet {
      font-size: 7.6rem;
      line-height: 11.5rem;
    }

    @include desktop {
      font-size: 9.6rem;
      min-width: 28rem;
    }
  }

  div {
    padding-top: 0.8rem;
    margin-left: 2.8rem;

    @include desktop {
      padding-top: 1.8;
    }

    @include tablet {
      margin-left: 4.8rem;
    }

    span {
      display: block;
      color: var(--color-white);
      font-size: 1.4rem;
      line-height: 1.8rem;

      @include tablet {
        font-size: 1.6rem;
      }

      &:first-child {
        font-size: 1.4rem;
        line-height: 1.8rem;
        font-weight: 700;
        margin-bottom: 1.6rem;

        @include tablet {
          font-size: 2.1rem;
          line-height: 2.3rem;
        }
      }
    }
  }
}
