.carousel {
  background-color: var(--color-black);

  &__item {
    height: 27.3rem;
    width: 10000rem;
    background-image: url('../img/carousel.png');
    background-size: contain;
    background-position-y: 3.1rem;
    animation: anim 1200s ease 0s infinite;
    background-repeat: no-repeat;
    background-size: auto 203px;
  }
}

@keyframes anim {
  0% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(-10000rem);
  }
}
