.collection {
  position: relative;

  @include tablet {
    padding-top: 10rem;
  }

  &__container {
    padding-bottom: 16.5rem;
  }

  &__title {
    margin-bottom: 4.1rem;
  }

  &__left {
    position: relative;
    z-index: 2;
  }

  &__text {
    font-size: 1.6rem;
    line-height: 2.2rem;
    color: var(--color-black);
    margin-bottom: 3.6rem;

    @include tablet {
      font-size: 1.8rem;
      max-width: 55rem;
    }

    span {
      font-family: var(--font-secondary);
      font-weight: 400;
      font-size: 6.6rem;
      line-height: 5.1rem;
      float: left;
      color: var(--color-primary);
      margin-right: 1rem;
      margin-bottom: 0.5rem;

      @include tablet {
        font-size: 8.6rem;
      }
    }
  }

  &__right {
    position: absolute;
    left: 50%;
    bottom: 0;
    z-index: 1;

    img {
      display: block;
      width: 100%;
      object-fit: cover;
    }
  }
}
