.socials {
  padding: 3.8rem 0px;
  background-color: var(--color-primary);

  @include tablet {
    padding: 7.8rem 0px;
  }

  &__container {
    padding-left: 5.2rem;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 8px;
      z-index: 1;
      width: 3px;
      height: 100%;
      background-color: var(--color-white);
    }

    @include tablet {
      padding-left: 8.2rem;
    }
  }

  &__link {
    display: inline-block;
    font-family: var(--font-primary);
    font-size: 1.8rem;
    line-height: 2rem;
    border-bottom: 1px solid var(--color-white);
    text-transform: uppercase;
    color: var(--color-white);
    position: relative;
    z-index: 1;

    &:hover {
      color: #000000;
      border-bottom: 1px solid transparent;
    }

    & + & {
      margin-left: 3rem;
    }

    @include tablet {
      & + & {
        margin-left: 5rem;
      }
    }
  }

  &__title {
    margin: 0;
    font-family: var(--font-secondary);
    font-size: 4rem;
    line-height: 4rem;
    font-weight: 400;
    color: var(--color-black);
    max-width: 105rem;

    @media (min-width: 576px) {
      font-size: 7rem;
      line-height: 6rem;
    }

    @include tablet {
      font-size: 10rem;
      line-height: 10rem;
    }

    @include desktop {
      font-size: 15rem;
      line-height: 15rem;
    }
  }
}
