.faq-section {
    background-color: var(--color-black);
    padding-top: 5rem;
    width: 100%;
    overflow: hidden;
}

.faq {
    display: flex;
    flex-direction: column;
    

    @include tablet {
        flex-direction: row;
        justify-content: space-around;
    }

    &__left {
        margin-bottom: 3rem;

        @include tablet {
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            margin-bottom: 0;
            width: 50%;

            img {
                width: auto;
            }
        }

        img {
            display: block;
            width: 100%;
            object-fit: cover;
        }
    }

    &-section__title {
        color: var(--color-white);
        margin-bottom: 5rem;
        position: relative;
        z-index: 2;
        &::before {
            content: "";
            position: absolute;
            top: 50%;
            right: -85%;
            z-index: -1;
            width: 187%;
            height: 8px;
            transform: translateY(11px);
            background-color: var(--color-primary);
        }
    }

    &__list {

        &-item {
            margin-bottom: 3.5rem;
            padding-left: 1.7rem;
            h3 {
                font-size: 1.7rem;
                line-height: 2.3rem;
                font-weight: 700;
                color: var(--color-white);
                margin-bottom: 1rem;
                position: relative;

                @include desktop {
                       font-size: 2.1rem;
                        line-height: 2.3rem;
                }

                &::after {
                    content: "";
                    position: absolute;
                    top: 0;
                    left: -1.7rem;
                    z-index: 1;
                    width: 3px;
                    height: 100%;
                    background-color: var(--color-primary);
                }
            }

            span {
                display: block;
                max-width: 45.5rem;
                // font-size: 16px;
                // line-height: 21px;
                font-size: 1.4rem;
                line-height: 2.1rem;
                color: rgba(255, 255, 255, 0.8);
                margin-bottom: 1.4rem;

                a {
                    color: inherit;
                }

                @include tablet {
                    margin-bottom: 2.8rem;
                }
            }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}

