@import "../mixins.scss";

.story {
    padding: 5rem 0;

    @include desktop {
        padding: 10rem 0;
    }

    &__container {
        display: flex;
        flex-direction: column;
      
        @include desktop {
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }
    }

    &__left {
        margin-bottom: 4rem;

        @include desktop {
            margin-bottom: 0rem;
            flex: 0 0 50%;
        }

        img {
            width: 100%;
            object-fit: cover;
        }
    }

    &__right {
        
        @include desktop {
            padding-left: 10rem;
            flex: 0 0 50%;
        }

        p {
            font-family: var(--font-primary);
            font-size: 1.6rem;
            line-height: 2.2rem;
            color: var(--color-black);
            margin-bottom: 3rem;

            @include tablet {
                font-size: 1.8rem;
            }
        }
    }

    &__text {
        

        &::first-letter {
            font-family: var(--font-secondary);
            font-size: 86px;
            color: var(--color-primary);
            float: left;
            margin-right: 1rem;
            margin-bottom: 0.5rem;
            font-weight: 400;
            font-size: 8.6rem;
            line-height: 5.1rem;
        }


    }
}