@import '../mixins.scss';
.hero {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;

  &__container {
    width: 100%;
    min-height: 100vh;
    display: flex;
    align-items: center;
  }

  &__left {
    position: relative;
    z-index: 2;
  }

  &__title {
    margin: 0;
    font-family: var(--font-secondary);
    font-weight: 400;
    color: var(--color-primary);
    font-size: 7rem;
    line-height: 8rem;
    margin-bottom: 1rem;

    span {
      display: block;
      font-size: 4.6rem;
      line-height: 2rem;
      color: var(--color-black);

      @include desktop {
        font-size: 9.6rem;
        line-height: 13rem;
      }

      @include mobile {
      }
    }

    @include tablet {
      font-size: 14rem;
      line-height: 13rem;
    }

    @include desktop {
      font-size: 19rem;
      line-height: 13rem;
      margin-bottom: 4.1rem;
    }
  }

  &__text {
    font-family: var(--font-primary);
    font-size: 1.6rem;
    line-height: 2.2rem;
    color: var(--color-black);
    margin-bottom: 3.6rem;
    max-width: 50rem;

    @include tablet {
      font-size: 1.8rem;
    }
  }

  &__right {
    position: absolute;
    left: 55%;
    bottom: 0;
    z-index: 1;

    img {
      display: block;
      width: 100%;
      object-fit: cover;
    }
  }
}
