@import "../mixins.scss";
.team-section {
  padding: 5rem 0;
}

.team-wrap {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -2rem;

  @media (min-width: 1024px) {
    margin: 0 -4rem;
  }
}

.team__title {
  margin-bottom: 4rem;

  @include desktop {
    margin-bottom: 0;
  }
}

.team {
  width: 100%;
  padding: 0 2rem;
  margin-bottom: 4rem;

  @media (min-width: 600px) {
    width: 50%;
  }

  @media (min-width: 1024px) {
    width: 25%;
    padding: 0 4rem;
  }

  &:nth-child(2n - 1) {
    @media (min-width: 1024px) {
      margin-top: 5.5rem;
    }
  }

  &__body {
    display: flex;
    flex-direction: column;
  }

  &__img {
    margin-bottom: 2rem;

    img {
      display: block;
      width: 100%;
    }
  }

  &__name {
    font-family: var(--font-secondary);
    color: var(--color-black);
    line-height: 1;
    font-size: 4.8rem;
    margin-bottom: 0.8rem;

    &::first-letter {
      color: var(--color-primary);
    }
  }

  &__position {
    color: var(--color-black);
    font-size: 16px;
  }

  &:last-child {
    margin-bottom: 0;
  }
}
