.footer {
    padding: 3.3rem 0;

    &__container {
        display: flex;
        flex-direction: column;
        align-items: center;

        @include tablet {
            flex-direction: row;
            justify-content: space-between;
        }
    }

    &__design {
        font-size: 1.6rem;
        line-height: 1.8rem;
        font-weight: 500;
        color: var(--color-black);
        margin-bottom: 1rem;

        @include tablet {
            margin-bottom: 0;
        }
    }

    &__copyright {
        font-size: 1.2rem;
        line-height: 1.8rem;
        color: var(--color-black);

        @include tablet {
            font-size: 1.4rem;
        }
    }
}